@import '../Variables/variables';

.header_container{
    padding: 15px;
    width: 100%;
    background-color: $violet;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    .logout-btn-hide{
        opacity: 0;
    }
    .logout-btn{
        position: absolute;
        top: 7vh;
        right: 0;
        width: 8rem;
        height: 2.5rem;
        background-color: #fff;
        border: none;
        font-weight: 600;
        cursor: pointer;
        display: flex;
        justify-content: space-around;
        align-items: center;
        outline: none;
        transition: all 0.2s ease-in-out;
        &:hover .logout-icon{
            color: red;
            path{

                color: red;
                stroke: red;
            }
        }
    }
    h1{
        font-family: $logo;
        color: white;
        font-weight: 300;
        font-size: 30px;
        cursor: pointer;

        strong{
            font-weight: 600;
        }
    }

    .search_container{
        width: 50%;
        height: 40px;
        border-radius: 5px;
        background-color: white;
        display: flex;
        justify-content: space-between;
        
        button{
            outline: none;
            border: none;
            background-color: $violet;
            color: white;
            height: 35px;
            margin: 2.5px;
            font-family: $raleway;
            font-size: 17px;
            font-weight: 600;
            border-radius: 5px 0 0 5px;
            padding: 0 8px 0;
            display: flex;
            align-items:center;
            justify-content: space-between;
            cursor: pointer;

            .MuiSvgIcon-root{
                margin: 0 0 0 3px;
                color: white;
                width: 25px;
            }
        }

        .search_input{
            width: 100%;
            display: flex;
            flex-direction: column;
            height: fit-content;
            input{
                width: 100%;
                outline: none;
                border: 0;
                margin: 0 15px 0;
                padding: 10px;
                &:focus{
                    outline: none;
                    border: 0;
                }
            }
        }

        .MuiSvgIcon-root{
            height: 100%;
            width: 32px;
            color: black;
        }
    }

    .MuiSvgIcon-root{
        color: white;
        width: 30px;
        height: 50%;
    }

    img{
        width: 40px;
        object-fit: contain;
        border-radius: 40px;
        cursor: pointer;
    }
}




@media screen and (max-width:768px) {
    .header_container{
        justify-content: space-between;
        .logout-btn{
            position: absolute;
            top: 85vh;
            right: 0;
            width: 8rem;
            height: 2.5rem;
            background-color: #fff;
            border: none;
            font-weight: 600;
        
        }
        h1{
    
            font-size: 18px;
        }
    
        .search_container{
            width: 50%;
            height: 30px; 
            font-size: 5px;

            button{
                display: none;
            }
            h1{
                margin: 0%;
            }
            .search_input{
                width: 80%;
                input{
                    
                    padding: 5px;
                }

            }
    
            .MuiSvgIcon-root{
                width: 25px;
                display: block;
            }
        }
    
        .MuiSvgIcon-root{
            display: none;
        }
        img{
            margin-left: auto;
            display: none;
        }
    }    
}